.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    padding-right: 4px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
  
  @for $index from 1 to 5 {
    & :nth-child(-n + #{$index}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
